.language-switcher {
  position: relative;
  display: flex;
  align-items: center;
}

.language-button {
  background: transparent;
  border: 1px solid #3F2305;
  color: #3F2305;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.language-button:hover {
  background: rgba(180, 123, 132, 0.2);
  transform: translateY(-2px);
}

.language-button:active {
  transform: translateY(0);
}

.language-flag {
  font-size: 18px;
}

.language-code {
  font-weight: 600;
}

.dropdown-arrow {
  font-size: 12px;
  transition: transform 0.3s ease;
}

.language-switcher:has(.language-button:focus) .dropdown-arrow {
  transform: rotate(180deg);
}

.language-dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background: #FFE7E7;
  border: 1px solid #B47B84;
  border-radius: 4px;
  margin-top: 4px;
  min-width: 180px;
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.15);
  z-index: 1000;
  animation: slideDown 0.2s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.language-option {
  background: none;
  border: none;
  padding: 10px 12px;
  width: 100%;
  text-align: left;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  color: #3F2305;
  font-size: 14px;
  transition: background 0.2s ease;
}

.language-option:not(:last-child) {
  border-bottom: 1px solid #B47B84;
}

.language-option:hover {
  background-color: #CAA6A6;
  border-bottom: 1px solid #3F2305;
}

.language-option.active {

  color: #944E63;
  font-weight: 600;
}

.option-flag {
  font-size: 16px;
}

.option-text {
  flex: 1;
}

/* Mobile responsive */
@media (max-width: 480px) {
  .language-button {
    padding: 5px 8px;
    font-size: 12px;
    border: 1px solid #3F2305;
  }

  .language-flag {
    font-size: 14px;
  }

  .language-code {
    display: none;
  }

  .dropdown-arrow {
    display: none;
  }

  .language-dropdown {
    min-width: 160px;
    font-size: 13px;
  }

  .language-option {
    padding: 8px 10px;
    font-size: 13px;
  }

  .option-flag {
    font-size: 14px;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .language-button {
    padding: 6px 10px;
    font-size: 13px;
  }

  .language-flag {
    font-size: 16px;
  }

  .language-dropdown {
    min-width: 170px;
  }
}